	module.exports = {"data":{"markdownRemark":{"html":"<p>천안문 광장까지 산책. 여기 축제 분위기가 있고, 국경일이 다가오고 있습니다. 톈안먼 타워가 개조되었으며 붉은 벽이 그려져 있습니다. 젊은 개척자들은 진수강 유역에 서있었습니다.</p>\n<p>광장에는 큰 변화가 있으며 광장 중앙에는 넓은 녹지대가 형성됩니다. 동쪽에는 꽃으로 만든 용이 있고, 서쪽에는 피닉스가있다. 그 뒤에는 꽃으로 만든 탑과 \"바다 세계\"라는 배 모델이있다. 많은 사람들이 사각형에 모여 빛을 발하고 사진을 찍고 지켜 보았습니다. 장안 거리의 거대한 구호가 세워졌고 그 깃발은 바람에 펄럭였다. 공화국의 생일이 다가오고 있습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0147.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0147.md","frontmatter":{"title":"9월 24일 토요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0147.md absPath of file >>> MarkdownRemark"}}

